body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.box{
	width: 100%;
	height: 100%;
	background: #eeeeee;
  }
  .box h1{
	color: #090910;
	text-transform: uppercase;
	font-weight: 500;
  }
  .box input[type = "text"],.box input[type = "password"]{
	border:0;
	background: #090910;
	display: block;
	margin: 20px auto;
	text-align: center;
	border: 2px solid #090910;
	padding: 2% 2%;
	min-width: 250px;
	width: 35%;
	outline: none;
	color: #eeeeee;
	border-radius: 24px;
	-webkit-transition: 0.25s;
	transition: 0.25s;
  }
  .box input[type = "text"]:focus,.box input[type = "password"]:focus{
	width: 40%;
  }
  .box input[type = "submit"]{
	border:0;
	background: #090910;
	display: block;
	margin: 20px auto;
	text-align: center;
	min-width: 150px;
	padding: 2% 3%;
	outline: none;
	color: #eeeeee;
	border-radius: 24px;
	-webkit-transition: 0.25s;
	transition: 0.25s;
	cursor: pointer;
  }
  .box input[type = "submit"]:hover{
	background: #cc0000;
  }


#logo{
	max-width: 300px;
}
